.relatedProduct {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  height: 90vh;
}
.relatedProduct h1 {
  color: #ff4545;
  font-weight: 600;
}
.relatedProduct hr {
  width: 200px;
  height: 6px;
  border: none;
  background: #ff4545;
  border-radius: 35px;
  margin: 20px;
}
.relatedProduct-item {
  display: flex;
  gap: 10px;
  padding-bottom: 70px;
}
