.newsletter {
  width: 65%;
  height: 50vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  background: linear-gradient(180deg, #fde1ff, #e1ffea22 60%);
  gap: 50px;
  margin: auto;
  margin-bottom: 100px;
  padding: 0px 140px;
}
.newsletter h1 {
  font-weight: 600;
  font-size: 55px;
  color: #454545;
  margin-top: 50px;
}
.newsletter p {
  font-size: 20px;
  color: #454545;
}
.input {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #eaeaea;
  width: 730px;
  border-radius: 80px;
  height: 70px;
  border: 1px solid #eaeaea;
}
.input input {
  width: 500px;
  margin-left: 30px;
  border: none;
  outline: none;
  color: #616161;
  font-family: Poppins;
  font-size: 16px;
}
.input button {
  padding: 27px;
  border-radius: 80px;
  border: none;
  background: #ff4545;
  color: #eaeaea;
  font-size: 15px;
  font-weight: 600;
}
