.NewCollection {
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 100px;
}
.NewCollection h1 {
  color: #ff4545;
}
.NewCollection hr {
  height: 6px;
  width: 10%;
  border-radius: 35px;
  background: #ff4545;
  border: none;
}
.collections {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  margin-top: 30px;
  gap: 30px;
}
