.productdisplay {
  display: flex;
  margin: 0px 170px;
}
.productdisplay-left {
  display: flex;
  gap: 17px;
}
.productdisplay-img-List {
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.productdisplay-img-List img {
  height: 143px;
}
.productdisplay-main-img {
  width: 566px;
  height: 619px;
}
.productdisplay-right {
  margin: 0px 70px;
  display: flex;
  flex-direction: column;
}
.productdisplay-right h1 {
  color: #ff4545;
  font-size: 30px;
  font-weight: 700;
}
.productdisplay-star-img {
  display: flex;
  align-items: center;
  margin-top: 13px;
  gap: 5px;
  color: #585858;
  font-size: 14px;
}
.productdisplay-right-prices {
  display: flex;
  margin: 40px 0px;
  gap: 15px;
  font-weight: 700;
}
.productdisplay-right-oldprice {
  color: #8c8c8c;
  text-decoration: line-through;
}
.productdisplay-right-newprice {
  color: #219b35;
}
.productdisplay-right-size h1 {
  margin-top: 55px;
  color: #8c8c8c;
  font-size: 20px;
  font-weight: 600px;
}
.productdisplay-right-size-chart {
  display: flex;
  margin: 30px 0px;
  align-items: center;
  gap: 17px;
}

.productdisplay-right-size-chart div {
  padding: 18px 24px;
  border: #585858 1px solid;
  border-radius: 3px;
  cursor: pointer;
}
.productdisplay-right-size-chart div:hover {
  background: #dddddd;
  transition: all 0.3s ease-out;
  transform: scale(1.1);
}
.productdisplay-right button {
  padding: 20px 40px;
  width: 200px;
  border-radius: 25px;
  border: none;
  background: #ff4545;
  color: #eaeaea;
  font-size: 16px;
  font-weight: 700;
  cursor: pointer;
  outline: none;
  transition: 0.3s ease-out;
}
.productdisplay-right button:hover {
  transition: all 0.3s ease-out;
  transform: scale(1.1);
}
.productdisplay-right-category {
  margin-top: 10px;
}
.productdisplay-right-category span {
  color: #8c8c8c;
}
