.watches {
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 20px;
  margin-bottom: 100px;
}
.watches h1 {
  color: #ff4545;
}
.watches hr {
  height: 6px;
  width: 10%;
  border-radius: 35px;
  background: #ff4545;
  border: none;
}
.watches-item {
  display: flex;
  margin-top: 50px;
  gap: 40px;
}
.watches-item img {
  width: 350px;
  height: 430px;
}
