}
.loginsignup {
  width: 100%;
  height: 80vh;
  padding-top: 100px;
  background: #fde1ff;
}
.loginsignup-container {
  width: 580px;
  height: 600px;
  margin: auto;
  background: #ffff;
  padding: 40px 60px;
}
.loginsignup-container h1 {
  text-align: center;
}
.loginsignup-field {
  display: flex;
  flex-direction: column;
  gap: 30px;
  margin-top: 30px;
}
.loginsignup-container h1 {
  margin: 20px 0px;
}
.loginsignup-field input {
  height: 70px;
  width: 100%;
  border: 1px solid #9e9898;
  color: #9e9898;
  outline: none;
  padding-left: 20px;
  font-size: 18px;
}
.loginsignup-container button {
  height: 70px;
  width: 104%;
  background: #ff4545;
  color: #eaeaea;
  border: none;
  font-size: 22px;
  font-weight: 600;
  cursor: pointer;
  margin-top: 30px;
}
.loginsignup-login {
  margin-top: 20px;
  color: #9e9898;
  font-size: 18px;
  font-weight: 500;
}
.loginsignup-login span {
  color: #ff4545;
  font-weight: 600;
}
.loginsignup-agree {
  display: flex;
  align-items: center;
  margin-top: 25px;
  gap: 20px;
  font-weight: 500;
  color: #9e9898;
}
