.popular {
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 10px;
  height: 90vh;
}
.popular h1 {
  color: #ff4545;
  font-weight: 600;
}
.popular hr {
  width: 200px;
  height: 6px;
  border-radius: 6px;
  background: #ff4545;
  border: none;
}
.popular-item {
  margin-top: 50px;
  display: flex;
  align-items: center;
  gap: 30px;
}
