.offer {
  display: flex;
  margin: auto;
  width: 70%;
  height: 60vh;
  padding: 0px 140px;
  margin-bottom: 150px;
  background: linear-gradient(180deg, #fde1ff, #e1ffea22 60%);
}
.offer-left {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.offer-left h1 {
  color: #171717;
  font-size: 80px;
  font-weight: 600;
}
.offer-left p {
  color: #171717;
  font-size: 22px;
  font-weight: 600;
}
.offer-left button {
  width: 282px;
  height: 70px;
  border-radius: 75px;
  margin-top: 30px;
  background: #ff4545;
  font-size: 22px;
  color: #eaeaea;
  font-weight: 500;
  border: none;
}
.offer-left button:hover {
  transform: scale(1.1);
  transition: all 0.3s ease-out;
}
.offer-right {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-top: 50px;
}
