.hero {
  height: 100vh;
  background: linear-gradient(180deg, #fde1ff, #e1ffea22 60%);
  display: flex;
}
.hero-left {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 20px;
  padding-left: 180px;
  line-height: 1.1;
}
.hero-left h2 {
  color: #090900;
  font-size: 26px;
  font-weight: 600px;
}
.hero-left p {
  color: #171717;
  font-size: 100px;
  font-weight: 700px;
}
.hero-latest-button {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  width: 310px;
  height: 70px;
  border-radius: 75px;
  margin-top: 30px;
  background: #ff4545;
  font-size: 22px;
  color: #eaeaea;
  font-weight: 500;
}
.hero-latest-button:hover {
  transform: scale(1.1);
  transition: all 0.3s ease-out;
}
