.discriptionbox {
  margin: 120px 170px;
}
.discriptionbox-navigator {
  cursor: pointer;
  display: flex;
}
.discriptionbox-navbox {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  font-weight: 600;
  width: 171px;
  height: 70px;
  border: 1px solid #eaeaea;
}
.discriptionbox-navbox.fade {
  color: #b0adad;
}
.discriptionbox-discription {
  display: flex;
  flex-direction: column;
  gap: 25px;
  border: 1px solid #eaeaea;
  padding: 48px;
  padding-bottom: 70px;
}
