@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300&family=Tomorrow:wght@300&display=swap");
.navbar {
  display: flex;
  justify-content: space-around;
  padding: 16px;
  background: #ff4545;
  height: 100px;
  font-family: "Roboto", sans-serif;
  font-family: "Tomorrow", sans-serif;
}
.logo {
  flex: 1;
  display: flex;
  align-items: center;
  gap: 10px;
}
.logo img {
  height: 50px;
}
.logo p {
  font-size: 27px;
  font-weight: 700;
  color: #eaeaea;
  cursor: pointer;
}

.logo hr {
  height: 1px;
  width: 100%;
  border-radius: 25px;
  background: #eaeaea;
}
.nav-items {
  display: flex;
  align-items: center;
  padding: 1.7rem;
  list-style: none;
  color: #eaeaea;
  font-size: 1.5rem;
  font-weight: 500;
  gap: 40px;
  font-family: Georgia, "Times New Roman", Times, serif;
}
.nev-items li {
  margin-bottom: 0px;
  cursor: pointer;
  position: absolute;
}
.nav-items li:hover {
  transition: all 0.3s ease-out;
  transform: scale(1.1);
}
.nav-login-cart {
  display: flex;
  align-items: center;
  gap: 45px;
}
.nav-login-cart button {
  width: 150px;
  height: 58px;
  border-radius: 75px;
  font-size: 1.5rem;
  background-color: white;
  color: #ff4545;
  cursor: pointer;
  border: none;
}
.nav-login-cart button:hover {
  font-weight: 1000;
  font-size: 1.6rem;
  transition: all 0.3s ease-out;
  transform: scale(1.1);
}
.nav-login-cart-count {
  color: #eaeaea;
  width: 22px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: -27px;
  margin-left: -55px;
  border-radius: 15px;
  font-size: 14px;
  background-color: red;
  color: #dad8d8;
  font-weight: 900;
}
.nav-login-cart-count:hover {
  transition: all 0.3s ease-out;
  transform: scale(1.1);
}
.nav-login-cart img {
  height: 2.5rem;
}

.subMenu li {
  list-style: none;
}
hr {
  padding: 0px;
  margin: 0px;
}
