.footer {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-items: center;
  gap: 50px;
  background: #ff4545;
}
.footer-logo img {
  height: 50px;
}
.footer-logo {
  display: flex;
  align-items: center;
  gap: 20px;
}
.footer-logo p {
  color: #eaeaea;
  font-size: 48px;
  font-weight: 700;
}
.footer-link {
  display: flex;
  align-items: center;
  justify-content: space-around;
  gap: 50px;
  list-style: none;
  color: #eaeaea;
  font-weight: 700;
  font-size: 20px;
}
.footer-link li {
  cursor: pointer;
}
.footer-social-icon {
  display: flex;
  align-items: center;
  gap: 20px;
}
.footer-social-icon-containter {
  padding: 10px;
  padding-bottom: 6px;
  border: 1px solid #eaeaea;
}
.footer-copyright {
  display: flex;
  font-size: 20px;
  flex-direction: column;
  align-items: center;
  gap: 30px;
  width: 100%;
  margin-bottom: 30px;
  color: #eaeaea;
  font-weight: 700;
}
.footer-copyright hr {
  width: 80%;
  height: 1px;
  border: none;
  background: #eaeaea;
  border-radius: 10px;
}
