.PopularMen {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  height: 90vh;
}
.PopularMen h1 {
  color: #ff4545;
  font-weight: 600;
  align-items: center;
}
.PopularMen hr {
  width: 170px;
  height: 6px;
  border-radius: 75px;
  background: #ff4545;
  border: none;
}
.PopularMen-item {
  display: flex;
  margin-top: 50px;
  gap: 30px;
}
