.shopCategory .poster {
  width: 82%;
  margin: 30px auto;
}
.shopcategory-index {
  display: flex;
  margin: 0px 170px;
  justify-content: space-between;
  align-items: center;
}
.shopcategory-sort {
  padding: 10px 20px;
  border-radius: 30px;
  border: 1px solid #ff4545;
  font-weight: 600;
}
.shopcategory-product {
  margin: 20px 170px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  row-gap: 80px;
  column-gap: 30px;
}
.shopcategory-loadmore {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 150px auto;
  width: 233px;
  height: 69px;
  border-radius: 30px;
  border: 1px solid #ff4545;
  font-size: 18px;
  font-weight: 600;
}
